<template>
  <div>
    <div class="right-content2-detail relative">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination">
          <div slot="">
            <span class="table-shenhe mr10" @click="showModal('')"></span>
          </div>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div>
            <p></p>
            <a-input />
          </div>
          <div>
            <p></p>
            <a-input />
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "systemsetting",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 10, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    // 
    this.columns = [
      {
        title: "",
        dataIndex: "0",

        scopedSlots: { customRender: "0" }
      },
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 20; i++) {
      this.data.push({
        0: "app",
        1: "50"
      });
    }
  },

  methods: {
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



