<template>
  <div class="tixian">
    <div class="guanliyuan-top">
      <table class="right-content2-top">
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span>
              <a-button type="primary" icon="search"></a-button>
            </span>
          </td>
        </tr>
      </table>
    </div>
    <div class="guanliyuan-middle">
      <span>{{data.length}}</span>
      <div class="right">
        <a-button icon="folder-add" @click="showModal('')"></a-button>
      </div>
    </div>
    <div class="right-content2-detail">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination">
          <a slot="6" href="javascript:;" class="table-shenhe" @click="showModal('')"></a>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal!=''">
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
          </div>
          <div v-show="modal==''">
            <p></p>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "blacklist",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 10, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    this.columns = [
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",
        scopedSlots: { customRender: "6" }
      }
    ];
    for (let i = 0; i < 30; i++) {
      this.data.push({
        1: "2019-10-09 14:21:22",
        2: "2019-10-09 14:21:22",
        3: "1563" + i + "1" + i,
        4: "",
        5: ""
      });
    }
  },

  methods: {
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



