<template>
  <div>
    <div class="guanliyuan-top mb20">
      <table class="right-content2-top w01">
        <tr>
          <td>
            <span class="top-span"></span>
            <a-select defaultValue="0" @change="handleChange">
              <a-select-option value="0">----</a-select-option>
              <a-select-option value="1"></a-select-option>
              <a-select-option value="2"></a-select-option>
              <a-select-option value="3"></a-select-option>
            </a-select>
          </td>
          <td>
            <span class="top-span">ID</span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-range-picker format="YYYY-MM-DD" :placeholder="['', '']" />
          </td>
        </tr>
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>

          <td>
            <span class="right mr07">
              <a-button type="primary" icon="search"></a-button>
            </span>
          </td>
        </tr>
      </table>
    </div>
    <div class="right-content2-detail">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination">
          <span slot="11">
            <span class="table-shenhe" @click="showModal('')"></span>
            <span class="table-shenhe" @click="showModal('')"></span>
            <span class="table-shenhe" @click="showModal('')"></span>
            <span class="table-shenhe" @click="showModal('')"></span>
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal==''">
            <div>
              <p>ID</p>
              <a-input />
            </div>
            <div>
              <p>ID</p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p>URL</p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
          </div>
          <div v-show="modal==''">
            <p></p>
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-textarea row="6" />
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal==''">
            <p></p>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "shopmanagement",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 10, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    this.columns = [
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: " ",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },

      {
        title: "ID",
        dataIndex: "7",

        scopedSlots: { customRender: "7" }
      },
      {
        title: "",
        dataIndex: "8",
        scopedSlots: { customRender: "8" }
      },
      ,
      {
        title: "   ",
        dataIndex: "9",

        scopedSlots: { customRender: "9" }
      },
      { title: "", dataIndex: "10", scopedSlots: { customRender: "10" } },

      {
        title: "",
        dataIndex: "11",
        scopedSlots: { customRender: "11" }
      }
    ];
    for (let i = 0; i < 100; i++) {
      this.data.push({
        1: "2019-10-09 14:21:22",
        2: `135` + i + "1267349",
        3: `158` + i + "1267349",
        4: "",
        5: "456123sadasdds",
        6: "",
        7: "1000001",
        8: "",
        9: "1000001",
        10: "",
        11: "2019-10-09 14:21:22"
      });
    }
  },
  methods: {
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



