<template>
  <div class="tixian">
    <div class="guanliyuan-top mb20">
      <table class="right-content2-top w01">
        <tr>
          <td>
            <span class="top-span"></span>
            <a-select defaultValue="0" @change="handleChange">
              <a-select-option value="0">----</a-select-option>
              <a-select-option value="1"></a-select-option>
              <a-select-option value="2"></a-select-option>
              <a-select-option value="3"></a-select-option>
            </a-select>
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-range-picker format="YYYY-MM-DD" :placeholder="['', '']" />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
        </tr>
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-range-picker format="YYYY-MM-DD" :placeholder="['', '']" />
          </td>
          <td class="right mr014">
            <a-button type="primary" icon="search"></a-button>
            <a-button type="primary" icon="download"></a-button>
          </td>
        </tr>
      </table>
    </div>

    <div class="right-content2-detail">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination">
          <img style="width:50px;heigth:50px" slot="6" slot-scope="data" :src="data" class="pointer" @click="showModal('')" />
          <a slot="11" class="table-shenhe mr10" @click="showModal('')"></a>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal==''">
            <p></p> 
            <a-radio-group name="radioGroup" :defaultValue="1">
              <a-radio :value="1"></a-radio>
              <a-radio :value="2"></a-radio>
            </a-radio-group>
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-select defaultValue="0" @change="handleChange" class="w01">
              <a-select-option value="0"></a-select-option>
              <a-select-option value="1"></a-select-option>
              <a-select-option value="2"></a-select-option>
              <a-select-option value="3"></a-select-option>
            </a-select>
          </div>
          <img  class="appeal-modal-img" src="https://gss0.bdstatic.com/94o3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike92%2C5%2C5%2C92%2C30/sign=4b1e0ff44da98226accc2375ebebd264/faf2b2119313b07e6a5add8902d7912396dd8c48.jpg" v-show="modal==''">
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "rechargemanagement",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 8, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["8", "10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    this.columns = [
      {
        title: "ID",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "ID",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },

      {
        title: "",
        dataIndex: "7",

        scopedSlots: { customRender: "7" }
      },
      {
        title: "",
        dataIndex: "8",
        scopedSlots: { customRender: "8" }
      },
      ,
      {
        title: "",
        dataIndex: "9",

        scopedSlots: { customRender: "9" }
      },
      {
        title: "",
        dataIndex: "10",
        scopedSlots: { customRender: "10" }
      },
      {
        title: "",
        dataIndex: "11",
        scopedSlots: { customRender: "11" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 100; i++) {
      this.data.push({
        1: i + "20191009",
        2: "11723" + i,
        3: "11.00",
        4: "Classification",
        5: "17596902373 ",
        6: "https://gss0.bdstatic.com/94o3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike92%2C5%2C5%2C92%2C30/sign=4b1e0ff44da98226accc2375ebebd264/faf2b2119313b07e6a5add8902d7912396dd8c48.jpg",
        7: "",
        8: "2019-10-20 00:15:47",
        9: "",
        10: "2019-10-20 00:15:47"
      });
    }
  },

  methods: {
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



