<template>
  <div class="tixian">
    <div class="guanliyuan-top">
      <table class="right-content2-top">
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-range-picker format="YYYY-MM-DD" :placeholder="['', '']" />
          </td>
          <td width="140">
            <span class="right">
              <a-button type="primary" icon="search"></a-button>
            </span>
          </td>
        </tr>
      </table>
    </div>
    <div class="guanliyuan-middle">
      <span>: 20 : 20</span>
    </div>
    <div class="right-content2-detail">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination"></a-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "myteam",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 10, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    // 
    this.columns = [
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",

        scopedSlots: { customRender: "4" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 30; i++) {
      this.data.push({
        1: "2019-01-10",
        2: "",
        3: "10" + i,
        4: "20" + i
      });
    }
  },

  methods: {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



