<template>
  <div>
    <div class="guanliyuan-middle">
      <span>{{data.length}}</span>
      <div class="right">
        <a-button icon="folder-add" @click="showModal('')"></a-button>
      </div>
    </div>
    <div class="right-content2-detail">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination">
          <span slot="3">
            <span class="table-shenhe mr10" @click="showModal('')"></span>
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal!=''">
            <div>
              <p>ID</p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
          </div>
          <div v-show="modal==''">
            <p></p>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "bankmanagement",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 10, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    this.columns = [
      {
        title: "ID",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      }
    ];
    for (let i = 0; i < 30; i++) {
      this.data.push({
        1: `1` + i + "",
        2: ""
      });
    }
  },

  methods: {
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



