<template>
  <div class="tixian">
    <div class="guanliyuan-top">
      <table class="right-content2-top w01">
        <tr>
          <td>
            <span class="top-span"></span>
            <a-select defaultValue="0" @change="handleChange">
              <a-select-option value="0"></a-select-option>
              <a-select-option value="1"></a-select-option>
              <a-select-option value="2"></a-select-option>
              <a-select-option value="3"></a-select-option>
            </a-select>
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-select defaultValue="0" @change="handleChange">
              <a-select-option value="0"></a-select-option>
              <a-select-option value="1"></a-select-option>
              <a-select-option value="2"></a-select-option>
              <a-select-option value="3"></a-select-option>
            </a-select>
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>

            <a-range-picker format="YYYY-MM-DD" :placeholder="['', '']" />
          </td>
        </tr>
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-select defaultValue="0" @change="handleChange">
              <a-select-option value="0"></a-select-option>
              <a-select-option value="1"></a-select-option>
              <a-select-option value="2"></a-select-option>
            </a-select>
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>

          <td>
            <span class="top-span"></span>
            <a-range-picker format="YYYY-MM-DD" :placeholder="['', '']" />
          </td>
        </tr>
      </table>
    </div>
    <div class="guanliyuan-middle">
      <div class="right">
        <a-button></a-button>
        <a-button></a-button>
        <a-button @click="showModal('')"></a-button>
        <a-button></a-button>
        <a-button></a-button>
      </div>
    </div>
    <div class="right-content2-detail">
        <a-table
          :rowSelection="rowSelection"
          :columns="columns"
          :dataSource="data"
          bordered
          :pagination="pagination"
        >
          <template slot="title" class="tixian-title">
            <a-icon type="bars" />
            <a-icon type="folder-open" />
            <a-icon type="printer" />
          </template>
          <span slot="13" class="table-shenhe" @click="showModal('')"></span>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal==''">
            <div>
              <p></p>
              <a-select defaultValue="0" @change="handleChange" class="w01">
                <a-select-option value="0"></a-select-option>
                <a-select-option value="1"></a-select-option>
                <a-select-option value="2"></a-select-option>
                <a-select-option value="3"></a-select-option>
              </a-select>
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
          </div>
          <div v-show="modal==''">
            <div>
              <p></p>
              <a-select defaultValue="0" @change="handleChange" class="w01">
                <a-select-option value="0"></a-select-option>
                <a-select-option value="1"></a-select-option>
                <a-select-option value="2"></a-select-option>
                <a-select-option value="3"></a-select-option>
              </a-select>
            </div>
            <div>
              <p></p>
              <a-input />
            </div>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "cashmanagement",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 8, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ['8',"10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    this.columns = [
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },

      {
        title: "",
        dataIndex: "7",

        scopedSlots: { customRender: "7" }
      },
      {
        title: "",
        dataIndex: "8",
        scopedSlots: { customRender: "8" }
      },
      ,
      {
        title: "",
        dataIndex: "9",

        scopedSlots: { customRender: "9" }
      },
      {
        title: "",
        dataIndex: "10",
        scopedSlots: { customRender: "10" }
      },
      {
        title: "",
        dataIndex: "11",
        scopedSlots: { customRender: "11" }
      },
      {
        title: "",
        dataIndex: "12",
        scopedSlots: { customRender: "12" }
      },
      {
        title: "",
        dataIndex: "13",
        scopedSlots: { customRender: "13" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 100; i++) {
      this.data.push({
        1: i + "20191009",
        2: `135` + i + "1267349",
        3: "11723" + i,
        4: "94563" + i + "1" + i,
        5: "13044312301",
        6: "13044312301",
        7: "1062" + i + "",
        8: "2019-10-09 14:21:22",
        9: "",
        10: "220304387249879579",
        11: "2019-10-09 14:21:22",
        12: ""
      });
    }
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        onChange: (selectedRowKeys, selectedRows) => {},
        getCheckboxProps: record => ({
          props: {
            disabled: record.name === "Disabled User", // Column configuration not to be checked
            name: record.name
          }
        })
      };
    }
  },
  methods: {
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



