<template>
  <div>
    <div class="guanliyuan-middle">
      <span>{{data.length}}</span>
      <div class="right">
        <a-button icon="folder-add" @click="showModal('')"></a-button>
      </div>
    </div>
    <div class="right-content2-detail relative">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination">
          <div slot="7">
            <span class="table-shenhe mr10" @click="showModal('')"></span>
            <span class="table-shenhe" @click="showModal('')"></span>
          </div>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal!=''">
            <div>
              <p>ios</p>
              <a-input />
            </div>
            <div>
              <p>android</p>
              <a-input />
            </div>
            <div>
              <p>ios</p>
              <a-input />
            </div>
            <div>
              <p>android</p>
              <a-input />
            </div>
            <div>
              <p>ios</p>
              <a-input />
            </div>
            <div>
              <p>android</p>
              <a-input />
            </div>
          </div>
          <div v-show="modal==''">
            <p></p>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "editioncontrol",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 10, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    this.columns = [
      {
        title: "ID",
        dataIndex: "0",

        scopedSlots: { customRender: "0" }
      },
      {
        title: "ios",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "android",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "ios",
        dataIndex: "3",
        scopedSlots: { customRender: "3" }
      },
      {
        title: "android",
        dataIndex: "4",

        scopedSlots: { customRender: "4" }
      },
      {
        title: "ios",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "android",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },
      {
        title: "",
        dataIndex: "7",
        scopedSlots: { customRender: "7" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 40; i++) {
      this.data.push({
        0: i + 1,
        1: "V1.0." + i,
        2: "V1.0.6" + i,
        3: "2",
        4: "2",
        5: "https://fir.im/r7na",
        6: "https://fir.im/r7na"
      });
    }
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        onChange: (selectedRowKeys, selectedRows) => {},
        getCheckboxProps: record => ({
          props: {
            disabled: record.name === "Disabled User", // Column configuration not to be checked
            name: record.name
          }
        })
      };
    }
  },
  methods: {
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



