<template>
  <div class="tixian">
    <div class="guanliyuan-top mb20">
      <table class="right-content2-top w01">
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span">QQ</span>
            <a-input />
          </td>
          <td>
            <span class="top-span">ID</span>
            <a-input />
          </td>
        </tr>
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>

          <td>
            <span class="top-span">ID</span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>

            <a-range-picker format="YYYY-MM-DD" :placeholder="['', '']" />
          </td>
          <td>
            <span>
              <a-button type="primary" icon="search"></a-button>
            </span>
          </td>
        </tr>
      </table>
    </div>
    <div class="right-content2-detail">
        <a-table
          :rowSelection="rowSelection"
          :columns="columns"
          :dataSource="data"
          bordered
          :pagination="pagination"
        >
          <span slot="1">
            
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
          <span slot="5" slot-scope="text">
            {{text}}
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
          <span slot="6" slot-scope="text">
            {{text}}
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
          <span class="table-shenhe" slot="9" @click="showModal('')" slot-scope="text">{{text}}</span>
          <a-popover trigger="click" placement="topLeft" slot="13">
            <template slot="content">
              <p class="pointer"></p>
              <p class="pointer" @click="showModal('')"></p>
              <p class="pointer" @click="showModal('')"></p>
            </template>
            <a-button></a-button>
          </a-popover>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal==''">
            <p></p>
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-radio-group name="radioGroup" :defaultValue="1">
              <a-radio :value="1"></a-radio>
              <a-radio :value="2"></a-radio>
              <a-radio :value="3"></a-radio>
            </a-radio-group>
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal=='' || modal=='' || modal=='' ">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal=='' || modal=='' || modal=='' ">
            <p></p>
            <a-textarea :rows="4" />
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-textarea :rows="4" />
          </div>
          <div v-show="modal==''">
            <div>
              <span class="top-span"></span>
              <a-input />
            </div>
            <div>
              <span class="top-span"></span>
              <a-input />
            </div>
            <div>
              <p class="top-span"></p>
              <a-range-picker  class="w01" format="YYYY-MM-DD" :placeholder="['', '']" />
            </div>
            <span class="right mb20">
              <a-button type="primary" class="mr0" icon="search"></a-button>
            </span>
              <a-table :columns="columnsModal" :dataSource="dataModal" bordered :pagination="false"></a-table>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "sellermanagement",
  data() {
    return {
      data: [],
      columns: [],
      columnsModal: [],
      dataModal: [],
      modal: "",
      visible: false,
      pagination: {
        pageIndex: 1,
        pageSize: 8, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["8", "10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        onChange: (selectedRowKeys, selectedRows) => {},
        getCheckboxProps: record => ({
          props: {
            disabled: record.name === "Disabled User", // Column configuration not to be checked
            name: record.name
          }
        })
      };
    }
  },
  mounted() {
    // 
    this.columns = [
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "ID",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },
      {
        title: "QQ",
        dataIndex: "7",

        scopedSlots: { customRender: "7" }
      },
      {
        title: "",
        dataIndex: "8",
        scopedSlots: { customRender: "8" }
      },
      {
        title: "",
        dataIndex: "9",

        scopedSlots: { customRender: "9" }
      },
      {
        title: "ID",
        dataIndex: "10",
        scopedSlots: { customRender: "10" }
      },
      {
        title: "",
        dataIndex: "11",

        scopedSlots: { customRender: "11" }
      },
      {
        title: "",
        dataIndex: "12",
        scopedSlots: { customRender: "12" }
      },
      {
        title: "",
        dataIndex: "13",
        scopedSlots: { customRender: "13" }
      }
    ];
    this.columnsModal = [
      {
        title: "ID",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },
      {
        title: "",
        dataIndex: "7",
        scopedSlots: { customRender: "7" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 100; i++) {
      this.data.push({
        1: i + "20191009",
        2: `135` + i + "1267349",
        3: "2019-10-09 14:21:22",
        4: "13044312301",
        5: "63" + i,
        6: "12.00",
        7: "",
        8: "5" + i,
        9: "" + i,
        10: i + "202394",
        11: "http://www.ifg.com",
        12: "220309"
      });
    }
  },

  methods: {
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



