<template>
  <div>
    <div class="guanliyuan-top">
      <table class="right-content2-top w01">
        <tr>
          <td>
            <span class="top-span">ID</span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span">ID</span>
            <a-input />
          </td>
          <td width="140"></td>
        </tr>
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td width="140">
            <span class="right">
              <a-button type="primary" icon="search"></a-button>
            </span>
          </td>
        </tr>
      </table>
    </div>
    <div class="guanliyuan-middle">
      <span>{{data.length}}</span>
      <div class="right">
        <a-button icon="download"></a-button>
        <a-button icon="folder-add" @click="showModal('')"></a-button>
      </div>
    </div>
    <div class="right-content2-detail relative">
        <a-table
          class="mxh414"
          :rowSelection="rowSelection"
          :columns="columns"
          :dataSource="data"
          bordered
          :pagination="pagination"
        >
          <div slot="">
            <span class="table-shenhe mr10" @click="showModal('')"></span>
            <span class="table-shenhe" @click="showModal('')"></span>
          </div>
        </a-table>
      <a-button type="danger" class="juese-delete bottom-50"></a-button>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal!=''">
            <div>
              <p>ID</p>
              <a-select defaultValue="0" @change="handleChange" class="w01">
                <a-select-option value="0"></a-select-option>
                <a-select-option value="1"></a-select-option>
                <a-select-option value="2"></a-select-option>
                <a-select-option value="3"></a-select-option>
              </a-select>
            </div>
            <div>
              <p>ID</p>
              <a-checkbox-group v-show="modal==''" :options="plainOptions" v-model="checkedList" />
              <a-select v-show="modal==''" defaultValue="0" class="w01" @change="handleChange">
                <a-select-option value="0"></a-select-option>
                <a-select-option value="1">/admin/Business/transfer</a-select-option>
                <a-select-option value="2">/admin/Reflect/examine</a-select-option>
                <a-select-option value="3">/admin/Reflect/examinte</a-select-option>
              </a-select>
            </div>
          </div>
          <div v-show="modal==''">
            <p></p>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "rolepowermanagement",
  data() {
    return {
      data: [],
      columns: [],
      modal: "",
      visible: false,
      plainOptions: [
        "/admin/Business/transfer",
        "/admin/Reflect/examine",
        "/admin/Reflect/examinte"
      ],
      checkedList: [],
      pagination: {
        pageIndex: 1,
        pageSize: 10, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    // 
    this.columns = [
      {
        title: "ID",
        dataIndex: "0",

        scopedSlots: { customRender: "0" }
      },
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "ID",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",

        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },
      {
        title: "",
        dataIndex: "7",
        scopedSlots: { customRender: "" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 40; i++) {
      this.data.push({
        0: i + 1,
        1: "admin",
        2: "34" + i,
        3: "admin",
        4: "game",
        5: "index",
        6: "2019-03-12 12:02:20"
      });
    }
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        onChange: (selectedRowKeys, selectedRows) => {},
        getCheckboxProps: record => ({
          props: {
            disabled: record.name === "Disabled User", // Column configuration not to be checked
            name: record.name
          }
        })
      };
    }
  },
  methods: {
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



