<template>
  <div class="tixian">
    <div class="guanliyuan-top mb20">
      <div class="right-content2-top">
        <a-button class="mr20" :class="{'ant-btn-primary':topTab==1}" @click="topTab=1"></a-button>
        <a-button class="mr20" :class="{'ant-btn-primary':topTab==2}" @click="topTab=2"></a-button>
        <a-button class="mr20" :class="{'ant-btn-primary':topTab==3}" @click="topTab=3"></a-button>
        <div class="right">
          <span>
            <a-button type="primary" icon="search"></a-button>
          </span>
        </div>
      </div>
    </div>
    <div class="guanliyuan-middle">
      <span>{{data.length}}</span>
      <div class="right">
        <a-button icon="folder-add" @click="showModal('')"></a-button>
      </div>
    </div>
    <div class="right-content2-detail">
        <a-table :columns="columns" :dataSource="data" bordered :pagination="pagination">
          <span slot="4" slot-scope="text">
            {{text}}
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
          <span slot="5" slot-scope="text">
            {{text}}
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
          <span slot="6" slot-scope="text">
            {{text}}
            <span class="table-shenhe" @click="showModal('')"></span>
          </span>
          <span class="table-shenhe" slot="9"  @click="showModal('')"></span>
        </a-table>
    </div>
    <div>
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div v-show="modal==''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal==''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal!='' && modal!=''">
            <p></p>
            <a-input />
          </div>
          <div v-show="modal==''">
            <p></p>
          </div>
        </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: "substationchildmanagement",
  data() {
    return {
      data: [],
      columns: [],
      columnsModal: [],
      dataModal: [],
      modal: "",
      visible: false,
      topTab: 1,
      pagination: {
        pageIndex: 1,
        pageSize: 8, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["8", "10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },

  mounted() {
    // 
    this.columns = [
      {
        title: "ID",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },
      {
        title: "",
        dataIndex: "7",

        scopedSlots: { customRender: "7" }
      },
      {
        title: "",
        dataIndex: "8",
        scopedSlots: { customRender: "8" }
      },
      {
        title: "",
        dataIndex: "9",

        scopedSlots: { customRender: "9" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 100; i++) {
      this.data.push({
        1: i + "209",
        2: `1` + i,
        3: "100.00",
        4: "10.00",
        5: "3.00",
        6: "12.00",
        7: "",
        8: "2019-10-10 12:3" + i
      });
    }
  },

  methods: {
    showModal(param) {
      this.modal = param;
      this.visible = true;
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



